
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import FlexboxItem from 'vux/src/components/flexbox/flexbox-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'

export default {
components: {
Flexbox, FlexboxItem, Group, Badge, Popup, XTextarea, XButton
},
data: () => ({
detail: {},
show: false,
comment: '',
img: 'http://thumb.niutuku.com/960x1/8f/b8/8fb8fb2623afa6336e2be205718f5f0e.jpg'
}),
mounted () {
console.log(this.$route.query.id)
this.getDetail(this.$route.query.id)
},
methods: {
getDetail: function (id) {
this.$getData(this.$configs.api.news, `/${id}`, response => {
this.detail = response
})
},
submit: function () {
}
}
}



